import React, { useState, useEffect } from "react";
import "./Orders.css";
import { Popconfirm, message } from "antd";
import Axios from "axios";
import moment from "moment";
import { Link } from "react-router-dom";
const Orders = () => {
  useEffect(() => {
    fetchData();
  }, []);

  const [data, setData] = useState([]);

  const fetchData = () => {
    Axios.get("/orders/")
      .then((data) => {
        setData(data.data.data);
        console.log(data.data.data);
      })
      .catch((err) => console.log(err));
  };

  return (
    <div className="module-main-div">
      <h1 className="module-title">Orders</h1>
      <div className="module-inner-div-manage">
        <div id="manage-table">
          <div id="manage-table-header">
            <div className="header-inner-div-header">
              <p>SI NO</p>
            </div>
            <div className="header-inner-div-header">
              <p>Date</p>
            </div>
            <div className="header-inner-div-header">
              <p>Name</p>
            </div>
            <div className="header-inner-div-header">
              <p>Amount</p>
            </div>

            <div className="header-inner-div-header">
              <p>Status</p>
            </div>
            <div className="header-inner-div-header">
              <p>Order detail</p>
            </div>
          </div>
          <div className="manage-table-body">
            {data.map((item, i) => {
              return (
                <div className="manage-table-body-particular">
                  <div className="body-inner-div-body">
                    <p>{i + 1}</p>
                  </div>
                  <div className="body-inner-div-body">
                    <p>{moment(item.crte_dt).format("DD-MM-YYYY")}</p>
                  </div>
                  <div className="body-inner-div-body">
                    <p>{item.first_name}</p>
                  </div>
                  <div className="body-inner-div-body">
                    <p>{item.total_amount}</p>
                  </div>
                  <div className="body-inner-div-body">
                    <p>{item.state}</p>
                  </div>
                  <div className="body-inner-div-body">
                    <Link to={"orderdetail/" + item.order_id}>
                      <button id="edit-btn"> Orders detail</button>
                    </Link>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Orders;
