import React, { useState, useEffect } from "react";
import "./ManageCategory.css";
import { Popconfirm, message } from "antd";
import Axios from "axios";
import moment from "moment";
import { Link } from "react-router-dom";
import { EditFilled } from "@ant-design/icons";
import { DeleteFilled } from "@ant-design/icons";
import { Table, Tag, Space } from "antd";
import { Button } from "@material-ui/core";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import TextField from "@material-ui/core/TextField";
import Input from "../../../components/Input/Input";
import DialogActions from "@material-ui/core/DialogActions";

const ManageCategory = () => {
  useEffect(() => {
    fetchData();
  }, []);

  const [data, setData] = useState([]);
  const [open, setOpen] = React.useState(false);
  const [categoryName, setCategoryName] = useState("");
  const [editOpen, setEditOpen] = React.useState(false);
  const [editCategoryName, setEditCategoryName] = useState("");
  const [editCategoryId, setEditCategoryId] = useState("");
  const [editId, setEditId] = useState(0);


  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClickEditOpen = (id) => {
    setEditOpen(true);
    fetchDataList(id);
  };

  const handleEditClose = () => {
    setEditOpen(false);
  };

  const fetchDataList = (id) => {
    try {
      Axios.get("/category/" + id)
        .then((data) => {
          console.log(data.data.data[0]);
          let d = data.data.data[0];
          setEditCategoryName(d.category_name);
          setEditCategoryId(d.category_id);
        })
        .catch((err) => console.log(err));
    } catch (err) {
      alert("Network error");
    }
  };

  const update = () => {
    Axios.put("/category/Update", {
      category_name: editCategoryName,
      category_id: editCategoryId,
    })
      .then((data) => {
        alert("Category Updated Successfully");
        handleEditClose();
        fetchData();
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const handleClose = () => {
    setOpen(false);
  };

  const fetchData = () => {
    Axios.get("/category/")
      .then((data) => {
        setData(data.data.data);
        console.log(data.data.data);
      })
      .catch((err) => console.log(err));
  };

  const addPost = async (e) => {
    e.preventDefault();
    Axios.post("/category/v1/catalog/categories/add", {
      category_name: categoryName,
    })
      .then((data) => {
        message.warn(data.data.message);
        console.log(data);
        handleClose();
        fetchData();
        setCategoryName("");
      })
      .catch((err) => alert("Something went wrong"));
  };

  function deleteCategory(category_id) {
    Axios.delete("/category/" + category_id)
      .then((data) => {
        message.success("Category Deleted Successfully");
        fetchData();
      })
      .catch((err) => console.log(err));
  }
  
  const columns = [
    {
      title: 'S.No',
      key: 'index',
      render:(text,record,index)=>`${index+1}`,
    },
    {
      title: "Image",
      dataIndex: "image_url",
      key: "image_url",
      render: (i, data) => (
        <>
          <img src={data.img_url_1} alt={data.item_name} id="manage-img" />
        </>
      ),
    },
    {
      title: "Category Name",
      dataIndex: "category_name",
      key: "category_name",
    },
    {
      title: "Action",
      key: "action",
      render: (i, data) => (
        <Space size="middle">
           <Link
                      align="center"
                      onClick={() => {
                        handleClickEditOpen(data.category_id);
                        setEditId(data.category_id);
                      }}
                    >
                      <EditFilled />
                    </Link>
          <Popconfirm
            title="Are you sure to delete this Category?"
            onConfirm={() => deleteCategory(data.category_id)}
            onCancel={null}
            okText="Yes"
            cancelText="No"
          >
            <div className="body-inner-div-body" >
              <DeleteFilled />
            </div>
          </Popconfirm>
        </Space>
      ),
    },
  ];

  
  return (
    <div className="module-main-div">
      <h1 className="module-title">Category</h1>
      <div
        className="body-inner-div-body"
        style={{ marginLeft: 930, marginTop: -55 }}
      >
          <Button variant="contained" color="primary" onClick={handleClickOpen}>
            Add
          </Button>
      </div>
      <Dialog
          open={open}
          onClose={handleClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">Add New Category</DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              id="name"
              label="Category Name"
              type="text"
              style={{ width: "100%" }}
              value={categoryName}
              onChange={(e) => setCategoryName(e.target.value)}
            />
            <Input
              style={{ width: "100%" }}
              autoFocus={false}
              label="Category Image"
              type="file"
            />
          </DialogContent>
          <DialogActions>
            <Button
              onClick={handleClose}
              variant="contained"
              color="primary"
              color="primary"
            >
              Cancel
            </Button>
            <Button
              onClick={(e) => {
                addPost(e);
              }}
              color="primary"
              variant="contained"
              color="primary"
            >
              Submit
            </Button>
          </DialogActions>
        </Dialog>
        <div>
        <Dialog
          open={editOpen}
          onClose={handleClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">Update Category</DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              id="name"
              label="Category Name"
              type="text"
              value={editCategoryName}
              onChange={(e) => setEditCategoryName(e.target.value)}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleEditClose} color="primary">
              Cancel
            </Button>
            <Button
              onClick={() => {
                update();
              }}
              color="primary"
            >
              Update
            </Button>
          </DialogActions>
        </Dialog>
      </div>
      <div className="module-inner-div-manage">
        <Table
          dataSource={data}
          columns={columns}
          pagination={{ pageSize: 5 }}
          scroll={{ y: 400 }}
        />
      </div>
    </div>
     
    
  );
};


export default ManageCategory;
