import React, { useState, useEffect } from "react";
import "./ManageProduct.css";
import { Popconfirm, message } from "antd";
import Axios from "axios";
import moment from "moment";
import { Link } from "react-router-dom";
import { EditFilled } from "@ant-design/icons";
import { DeleteFilled } from "@ant-design/icons";
import { Table, Tag, Space } from "antd";

const ManageProduct = () => {
  useEffect(() => {
    fetchData();
  }, []);

  const [data, setData] = useState([]);

  const fetchData = () => {
    Axios.get("/product/")
      .then((data) => {
        setData(data.data.data);
        console.log(data.data.data);
      })
      .catch((err) => console.log(err));
  };

  function deleteProduct(item_id) {
    Axios.delete("/product/" + item_id)
      .then((data) => {
        message.success("Product Deleted Successfully");
        fetchData();
      })
      .catch((err) => console.log(err));
  }

  const columns = [
    {
      title: 'S.No',
      key: 'index',
      render:(text,record,index)=>`${index+1}`,
    },
    {
      title: "Image",
      dataIndex: "image_url",
      key: "image_url",
      render: (i, data) => (
        <>
          <img src={data.img_url_1} alt={data.item_name} id="manage-img" />
        </>
      ),
    },
    {
      title: "Product Name",
      dataIndex: "item_name",
      key: "address",
    },
    {
      title: "MSRP",
      dataIndex: "msrp",
      key: "msrp",
    },
    {
      title: "Price",
      dataIndex: "price",
      key: "price",
    },
    {
      title: "Action",
      key: "action",
      render: (i, data) => (
        <Space size="middle">
          <Link to={"editproduct/" + data.item_id}>
            <EditFilled />
          </Link>
          <Popconfirm
            title="Are you sure to delete this Product?"
            onConfirm={() => deleteProduct(data.item_id)}
            onCancel={null}
            okText="Yes"
            cancelText="No"
          >
            <div className="body-inner-div-body" style={{ fontSize: 20 }}>
              <DeleteFilled />
            </div>
          </Popconfirm>
        </Space>
      ),
    },
  ];

  return (
    <div className="module-main-div">
      <h1 className="module-title">Product</h1>
      <div
        className="body-inner-div-body"
        style={{ marginLeft: 930, marginTop: -55 }}
      >
        <Link to={"/addproduct/"}>
          <button id="add-btn">Add</button>
        </Link>
      </div>
      <div className="module-inner-div-manage">
        <Table
          dataSource={data}
          columns={columns}
          pagination={{ pageSize: 5 }}
          scroll={{ y: 400 }}
        />
      </div>
    </div>
  );
};

export default ManageProduct;
