import React, { useState, useEffect } from "react";
import Axios from "axios";
import { useParams } from "react-router-dom";

function Orderdetail() {
  let { id } = useParams();

  useEffect(() => {
    fetchData();
  }, []);

  const [data, setData] = useState([]);

  const fetchData = () => {
    Axios.get("/orders/orderDetail/" + id)
      .then((data) => {
        setData(data.data.data);
        console.log(data.data.data);
      })
      .catch((err) => console.log(err));
  };
  return (
    <div className="module-main-div">
      <h1 className="module-title">Orders</h1>
      <div className="module-inner-div-manage">
        <div id="manage-table">
          <div id="manage-table-header">
            <div className="header-inner-div-header">
              <p>Id</p>
            </div>
            <div className="header-inner-div-header">
              <p>Order ID</p>
            </div>
            <div className="header-inner-div-header">
              <p>Item Id</p>
            </div>
            <div className="header-inner-div-header">
              <p>Item Rate</p>
            </div>
            <div className="header-inner-div-header">
              <p>Quantity</p>
            </div>

            <div className="header-inner-div-header">
              <p>Amount</p>
            </div>
          </div>
          <div className="manage-table-body">
            {data.map((item, i) => {
              return (
                <div className="manage-table-body-particular">
                  <div className="body-inner-div-body">
                    <p>{i + 1}</p>
                  </div>
                  <div className="body-inner-div-body">
                    <p>{item.order_id}</p>
                  </div>
                  <div className="body-inner-div-body">
                    <p>{item.item_id}</p>
                  </div>
                  <div className="body-inner-div-body">
                    <p>{item.item_rate}</p>
                  </div>
                  <div className="body-inner-div-body">
                    <p>{item.quantity}</p>
                  </div>
                  <div className="body-inner-div-body">
                    <p>{item.item_amount}</p>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
}
export default Orderdetail;
