import React, { useState, useEffect } from "react";
import "../AddProduct/AddProduct.css";
import Axios from "axios";
import Input from "../../../components/Input/Input";
import Button from "../../../components/Button/Button";
import { useHistory } from "react-router-dom";
import Dropdown from "../../../components/Dropdown/Dropdown";
import { Link } from "react-router-dom";
import { ArrowLeftOutlined } from "@ant-design/icons";

const AddProduct = () => {
  useEffect(() => {
    fetchCategoryData();
  }, []);
  const [productName, setProductName] = useState("");
  const [productDescription, setProductDescription] = useState("");
  const [ingredients, setIngredients] = useState("");
  const [msrp, setMsrp] = useState("");
  const [price, setPrice] = useState("");
  const [grossWeight, setGrossWeight] = useState("");
  const [neWeight, setNetWeight] = useState("");
  const [weightUnit, setWeightUnit] = useState("");
  const [currency, setCurrency] = useState("");
  const [CategoryData, setCategoryData] = useState([]);
  const [category, setCategory] = useState("");
  const [brand, setBrand] = useState([]);
  const [brandId, setBrandId] = useState([]);

  const history = useHistory("");

  const save = (e) => {
    e.preventDefault();
    Axios.post("/product/items", {
      item_name: productName,
      item_desc: productDescription,
      ingredients: ingredients,
      msrp: msrp,
      price: price,
      gross_wt: grossWeight,
      net_wt: neWeight,
      wt_unit: weightUnit,
      currency: currency,
      category: category,
      brand: brandId,
    })
      .then((data) => {
        console.log(data);
        history.push("/manageproduct");
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const fetchCategoryData = () => {
    Axios.get("/category/product")
      .then((data) => {
        setCategoryData(data.data);
        // console.log(data.data);
      })
      .catch((err) => console.log(err));
  };

  const brandData = (id) => {
    console.log(id);
    Axios.get(`/brand/category/brand/${id}`)
      .then((data) => {
        // console.log(data.data);
        setBrand(data.data);
      })
      .catch((err) => console.log(err));
  };

  return (
    <div className="module-main-div">
      <div id="module-title-div">
        <Link to={"/manageproduct/"}>
          <ArrowLeftOutlined style={{ fontSize: "22px", color: "#000" }} />
        </Link>
        <h1 className="module-title">Add Product</h1>
      </div>

      <div className="module-inner-div">
        <form id="addProduct-form" onSubmit={(e) => save(e)}>
          <div
            style={{
              width: "100%",
              alignContent: "flex-start",
              marginLeft: -350,
            }}
          >
            <h1 style={{ fontSize: 18, fontWeight: "bold" }}>
              {" "}
              Product Details
            </h1>
            <div>
              <Input
                autoFocus={true}
                label="Product Name"
                required={true}
                type="none"
                onChange={(e) => setProductName(e.target.value)}
              />
              <div style={{ marginTop: -101, marginLeft: 450, width: "100%" }}>
                <Input
                  label="Product Description"
                  type="none"
                  onChange={(e) => setProductDescription(e.target.value)}
                />
              </div>
            </div>
            <div>
              <Dropdown
                data={CategoryData}
                label="Category"
                onChange={(e) => {
                  setCategory(e);
                  brandData(e);
                }}
              />
              <div style={{ marginTop: -87, marginLeft: 450, width: "100%" }}>
                <Dropdown
                  data={brand}
                  label="Brand"
                  onChange={(e) => {
                    setBrandId(e);
                  }}
                />
              </div>
            </div>
          </div>

          <div
            style={{
              width: "100%",
              alignContent: "flex-start",
              marginLeft: -350,
              marginTop: 30,
            }}
          >
            <h1 style={{ fontSize: 18, fontWeight: "bold" }}>Price Details</h1>
            <div>
              <Input
                label="MSRP"
                type="none"
                onChange={(e) => setMsrp(e.target.value)}
              />
              <div style={{ marginTop: -101, marginLeft: 450, width: "100%" }}>
                <Input
                  label="Price"
                  type="none"
                  onChange={(e) => setPrice(e.target.value)}
                />
              </div>
            </div>
            <div>
              <Input
                label="Gross Weight"
                type="none"
                onChange={(e) => setGrossWeight(e.target.value)}
              />
              <div style={{ marginTop: -101, marginLeft: 450, width: "100%" }}>
                <Input
                  label="Net Weight"
                  type="none"
                  onChange={(e) => setNetWeight(e.target.value)}
                />
              </div>
            </div>
            <div>
              <Input
                label="Weight Unit"
                type="none"
                onChange={(e) => setWeightUnit(e.target.value)}
              />
              <div style={{ marginTop: -101, marginLeft: 450, width: "100%" }}>
                <Input
                  label="Currency"
                  type="none"
                  onChange={(e) => setCurrency(e.target.value)}
                />
              </div>
            </div>
          </div>

          <div
            style={{
              width: "100%",
              alignContent: "flex-start",
              marginLeft: -350,
              marginTop: 30,
            }}
          >
            <h1 style={{ fontSize: 18, fontWeight: "bold" }}>Images</h1>
            <div style={{ width: "50%" }}>
              <Input autoFocus={false} label="Product Image 1" type="file" />
            </div>
            <div style={{ marginTop: -101, marginLeft: 220, width: "50%" }}>
              <Input autoFocus={false} label="Product Image 2" type="file" />
            </div>
            <div style={{ marginTop: -101, marginLeft: 440, width: "50%" }}>
              <Input autoFocus={false} label="Product Image 3" type="file" />
            </div>
            <div style={{ marginTop: -101, marginLeft: 660, width: "50%" }}>
              <Input autoFocus={false} label="Product Image 4" type="file" />
            </div>
            <div>
              <Input
                label="Ingredients"
                type="none"
                onChange={(e) => setIngredients(e.target.value)}
              />
            </div>
          </div>

          <Button label="Save" />
        </form>
      </div>
    </div>
  );
};

export default AddProduct;
